.section-title{
    font-weight: normal;
    padding: 50px 0;
    margin: 0;
    position: relative;
    &::after, &::before{
        position: absolute;
        content: "";
        width: 80px;
        border-top: 1px solid $gray-800;
        left: 50%;
        top: 50%;
        margin-left: 80px;
    }

    &::after{
        margin-left: -160px;
    }
}


